<template>
    <div class="wrap">
        <!-- 质询 -->
        <van-search v-model="value" placeholder="标题"
                @search="onSearch"
        />
        <div class="content">
            <van-loading type="spinner" color="#1989fa" v-if="loading" />
            <div v-else>
                <List @click="handleClick" info />
                
            </div>
        </div>
    </div>
</template>

<script>
import List from "@/components/listUI"
import { Toast } from 'vant';

export default {
        data() {
        return {
        value: '',
        list: [],
        loading: true,
        finished: false,
        };
    },
    components: {
        List
    },
    mounted() {
        setTimeout(() => {
                // 加载状态结束
                this.loading = false;
            }, 1000);
    },
    methods: {
        handleClick(info) {
                this.$router.push({
                name: "activity",
                params: {
                    id: info.id,
                },
                query: {
                    info,
                }
            }
            )
        },
        onSearch(val) {

            Toast("功能暂未开放");
            this.value = ""
        },
    },
}
</script>

<style lang="scss" scoped>
    .wrap{
        background-color: rgb(232, 232, 232);
    }
    .van-loading {
        text-align: center;
    }
</style>