<template>
    <div class="content">
            <div class="img" v-for="item in list" :key="item.id"
                @click="handleClick(item)"
            >
                <h4 v-if="!info">{{item.name}}</h4>
                <img :src="item.src" :alt="item.name">
                <p :style="{
                    textAlignLast: info? 'justify': '',
                }"
                :class="{
                    info: info
                }"
                >
                    {{info? item.name: ""}}
                    {{ !info?"价格：￥"+item.pri: ""}}
                    <span>
                        {{info? "了解详情": ""}}
                        </span> 
                    </p>
            </div>
    </div>
</template>

<script>
import list from "@/utils"
const listMap = {
    list1: list.slice().concat(list).map((item, index)=> ({
        ...item, id: index,
    })),
    list2: list,
    list3: list.slice(0, 10),
    list4: list.slice(0, 9).concat(list).map((item, index)=> ({
        ...item, id: index,
    })),
}
export default {
    data() {
        return {
        }
    }, 
    props: {
        sort: {
            type: Boolean,
            default: false,
        },
        num: {
            type: String,
            default: "1",
        },
        info: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        list() {
            if(this.sort) {
                return listMap["list"+this.num].sort(function () {
                    return Math.random() - 0.5;
                })
            }
            return listMap["list"+this.num];
        }
    },
    methods: {
        handleClick(item) {
            this.$emit("click", item);
        }
    }
}
</script>

<style lang="scss" scoped>
    .content{
    background-color: rgb(232, 232, 232);

        margin-top: 10px;
        padding-bottom: 50px;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content:  space-evenly;
        .img{
            padding-top: 10px;
            h4{
                margin: 0;
                padding-bottom: 10px;
            }
            border-radius: 10px;
            background-color: #fff;
            margin-bottom: 10px;
            width: 173px;
            height: 220px;
            text-align: center;
            img{
                width: 100%;
                height: 63%;
                display: block;
                object-fit: contain;
            }
            p{
                margin: 0;
                padding-top: 10px;
                font-size: 14px;
                &.info{
                    margin-top: 50px;
                    padding: 0 10px;
                }
                span{
                    font-size: 14px;
                    color: rgb(133, 131, 131);
                }
            }
        }
    }
</style>